.main {
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  /* border-radius: 5px; */
  /* border: 2px solid #272727; */
  background-color: #f59e0b;
  overflow: hidden;
}

.fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: #2563eb; */
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #272727;
}

.container {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
}
