@media (min-width: 768px) {
  .md\:tw-space-y-0 > :not([hidden]) ~ :not([hidden]) {
    --tw-space-y-reverse: 0;
    margin-top: calc(0px * calc(1 - var(--tw-space-y-reverse)));
    margin-bottom: calc(0px * var(--tw-space-y-reverse));
  }

  .md\:tw-flex {
    display: -webkit-flex;
    display: flex;
  }

  .md\:tw-grid {
    display: grid;
  }

  .md\:tw-flex-col {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .md\:tw-justify-center {
    -webkit-justify-content: center;
            justify-content: center;
  }

  .md\:tw-text-sm {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }

  .md\:tw-text-lg {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }

  .md\:tw-text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  .md\:tw-text-6xl {
    font-size: 3.75rem;
    line-height: 1;
  }

  .md\:tw-p-1 {
    padding: 0.25rem;
  }

  .md\:tw-w-1\/2 {
    width: 50%;
  }

  .md\:tw-w-4\/12 {
    width: 33.333333%;
  }

  .md\:tw-grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (min-width: 1024px) {
  .lg\:tw-text-6xl {
    font-size: 3.75rem;
